import {
  AfterViewInit,
  ChangeDetectionStrategy,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  ViewChild
} from '@angular/core';
import { FileSelectionService } from '@sofico-framework/utils';
import { takeUntilDestroy, UntilDestroy } from 'ngx-reactivetoolkit';
import { EMPTY, fromEvent, merge, Observable, timer } from 'rxjs';
import {
  debounce,
  distinctUntilChanged,
  filter,
  map,
  mapTo,
  tap
} from 'rxjs/operators';

@UntilDestroy()
@Component({
  selector: 'sof-drop-file-area',
  changeDetection: ChangeDetectionStrategy.OnPush,
  styleUrls: ['./drop-file-area.component.scss'],
  template: `
    <div #dropArea class="drop-area p-3" [class.highlight]="highlight$ | async">
      <h4 class="mt-2">
        <sof-svg-icon icon="icon-file-empty" size="24"></sof-svg-icon>
        {{ dragAndDropText }}
        <a href="javascript:void(0)" (click)="showFileSelect()">{{
          browseFilesLinkText
        }}</a>
      </h4>
      <p>
        <ng-container *ngIf="!multipleFiles">
          {{ singleFileWarningMsg }}<br />
        </ng-container>
        <ng-content></ng-content>
      </p>
    </div>
  `
})
export class DropFileAreaComponent implements AfterViewInit, OnInit, OnDestroy {
  @Input() tc: string;
  @Input() dragAndDropText: string;
  @Input() browseFilesLinkText: string;
  @Input() singleFileWarningMsg: string;
  @Input() multipleFiles = true;
  /**
   * The default accepted MIME types are pdf, jpeg and png.
   */
  @Input() acceptedMimeTypes: string[] = [
    'application/pdf',
    'image/jpeg',
    'image/x-png'
  ];
  @Output() addedFiles = new EventEmitter<FileList>();

  @ViewChild('dropArea', { static: true })
  dropArea: ElementRef;

  dropAreaDropEvent$: Observable<DragEvent>;
  dropAreaDragEnterEvent$: Observable<DragEvent>;
  dropAreaDragOverEvent$: Observable<DragEvent>;
  dropAreaDragLeaveEvent$: Observable<DragEvent>;

  highlight$: Observable<boolean>;

  droppedFiles$: Observable<FileList>;

  constructor(private fileSelectionService: FileSelectionService) {}

  ngOnDestroy(): void {}

  ngOnInit(): void {
    this.dropAreaDropEvent$ = this.getDropEvent$(
      this.dropArea.nativeElement,
      'drop'
    );
    this.dropAreaDragEnterEvent$ = this.getDropEvent$(
      this.dropArea.nativeElement,
      'dragenter'
    );
    this.dropAreaDragOverEvent$ = this.getDropEvent$(
      this.dropArea.nativeElement,
      'dragover'
    );
    this.dropAreaDragLeaveEvent$ = this.getDropEvent$(
      this.dropArea.nativeElement,
      'dragleave'
    );

    this.highlight$ = this.getHighlight$();

    this.droppedFiles$ = this.getDroppedFiles$();
  }

  ngAfterViewInit(): void {
    if (this.multipleFiles) {
      this.droppedFiles$.pipe(takeUntilDestroy(this)).subscribe(fileList => {
        this.addedFiles.emit(fileList);
      });
    } else {
      this.droppedFiles$
        .pipe(
          filter(fileList => fileList?.length <= 1),
          takeUntilDestroy(this)
        )
        .subscribe(fileList => {
          this.addedFiles.emit(fileList);
        });
    }
  }

  showFileSelect(): void {
    this.fileSelectionService
      .getFileSelector(this.acceptedMimeTypes, this.multipleFiles)
      .pipe(takeUntilDestroy(this))
      .subscribe(fileList => this.addedFiles.emit(fileList));
  }

  private getDroppedFiles$(): Observable<FileList> {
    return this.dropAreaDropEvent$.pipe(
      map((e: DragEvent) => e.dataTransfer.files),
      filter(files =>
        Array.from(files).every(file =>
          this.acceptedMimeTypes.includes(file.type)
        )
      )
    );
  }

  private getHighlight$(): Observable<boolean> {
    return merge(
      merge(this.dropAreaDragEnterEvent$, this.dropAreaDragOverEvent$).pipe(
        mapTo(true)
      ),
      merge(this.dropAreaDropEvent$, this.dropAreaDragLeaveEvent$).pipe(
        mapTo(false)
      )
    ).pipe(
      // This delays un-highlighting the drop zone,
      // first added as a fix to eliminate flickering by dragLeave Events
      distinctUntilChanged(),
      debounce(startDevice => (startDevice ? EMPTY : timer(100))),
      distinctUntilChanged()
    );
  }

  private getDropEvent$(
    elementRef: any,
    eventName:
      | 'drag'
      | 'dragend'
      | 'dragenter'
      | 'dragexit'
      | 'dragleave'
      | 'dragover'
      | 'dragstart'
      | 'drop'
  ): Observable<DragEvent> {
    return fromEvent(elementRef, eventName).pipe(
      tap((e: Event) => {
        e.preventDefault();
        e.stopPropagation();
      })
    ) as Observable<DragEvent>;
  }
}
